import React, { useState } from 'react';
import { NavLink } from "react-router-dom";
import '../App.css';
import '../Styles/NavBar.css';
import '../Styles/Buttons.css';
import Logo from '../img/express.png'
import BurgerOn from '../img/burger-on.png'
import BurgerOff from '../img/burger-off.png'
import DropdownDown from '../img/dropdown-down.png'
import DropdownUp from '../img/dropdown-up.png'
import { NavDropdown, Accordion } from 'react-bootstrap';


const links = [
    {
        to: '/servise-1',
        label: 'Servise-1',
    },
    {
        to: '/servise-2',
        label: 'Servise-2',
    },
    {
        to: '/servise-3',
        label: 'Servise-3',
    }, {
        to: '/servise-4',
        label: 'Servise-4',
    }, {
        to: '/servise-5',
        label: 'Servise-5',
    },
];

function NavBar() {
    const [showLinks, setShowLinks] = useState(false);
    const [rotateDropArrow, setRotateDropArrow] = useState(false);
    return (
        <nav className='navbar'>
            <div className='ellipse_right' />
            <div className='ellipse_left' />
            <div className='conteiner'>
                <div className='navbar_wrapper flex'>
                    <img className='navbar_logo' src={Logo} alt='logo img' />
                    <nav className='navbar_nav flex' id={showLinks ? 'hidden' : ''}>
                        <NavLink to='wdm-test' className='navbar_link' activeClassName='active' onClick={() => setShowLinks(false)}>Home</NavLink>
                        <NavLink to='about-us' className='navbar_link' activeClassName='active' onClick={() => setShowLinks(false)}>About us</NavLink>
                        <div className='dropdown_wrapper flex'>
                            <NavDropdown onClick={() => setRotateDropArrow(!rotateDropArrow)} title='Our servise' flip='true' id="basic-nav-dropdown">
                                {links.map(link => (<NavDropdown.Item>
                                    <NavLink to={link.to} onClick={() => setShowLinks(false)}>{link.label}</NavLink>
                                </NavDropdown.Item>))}
                            </NavDropdown>
                            <img className='dropdown_arrow' src={!rotateDropArrow ? DropdownDown : DropdownUp} />
                        </div>
                        <Accordion defaultActiveKey="1" flush>
                            <Accordion.Item eventKey="0">
                                <Accordion.Header>Our servise</Accordion.Header>
                                <Accordion.Body>
                                    {links.map(link => (<NavLink to={link.to} onClick={() => setShowLinks(false)}>{link.label}</NavLink>))}
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <NavLink to='contact-us' className='navbar_link' activeClassName='active' onClick={() => setShowLinks(false)}>Contact us</NavLink>
                    </nav>
                    <div className='navbar_buttons flex'>
                        <div className='button_wrapper'>
                            <button className='button white_button'>Call us</button>
                            <div>Call us</div>
                        </div>
                        <div className='menu'>
                            <button onClick={() => setShowLinks(!showLinks)}>
                                <img src={!showLinks ? BurgerOn : BurgerOff}></img>
                            </button>
                            <div className='menu_text'>Menu</div>
                        </div>
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default NavBar;