import React from 'react';
import '../App.css';
import '../Styles/Footer.css';
import FooterLogo from '../img/footer-logo.png'
import Dmca from '../img/footer-dmca.png'

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'

function Footer() {
  return (
    <footer className='footer'>
      <div className='conteiner'>
        <div className='footer_wrapper'>
          <Row md={4} sm={2} xs={1}>
            <Col className='footer_copyright'>
              <img className='footer_logo' src={FooterLogo} alt='logo png' />
              <p>All Rights Reserved to info.com</p>
              <p>© DDD – Web Marketing</p>
              <p>Privacy Policy</p>
              <img className='footer_dmca' src={Dmca} alt='dmca png' />
            </Col>
            <Col className='footer_contact'>
              <h3>Contact us</h3>
              <div>
                <p>UK</p>
                <p>+44-2020202020 <br></br> +44-2020202020</p>
              </div>
              <div>
                <p>AUT</p>
                <p>+44-2020202020</p>
              </div>
              <div>
                <p>AUS</p>
                <p>+44-2020202020</p>
              </div>
            </Col>
            <Row md={1} sm={2} xs={1} className='footer_address'>
              <Col>
                <h3>Address</h3>
                <p>Lorem ipsum<br></br>set amet</p>
              </Col>
              <Col>
                <h3>Our office hours</h3>
                <p>Mon-Fr: 8:00-18:00 GMT<br></br>Sat/Su: Closed</p>
              </Col>
            </Row>
            <Col className='footer_email' >
              <h3>Email us</h3>
              <p>info@sterling.law</p>
            </Col>
          </Row>
        </div>
      </div>
    </footer >
  )
}

export default Footer;