import React, { useState } from 'react';
import '../App.css';
import '../Styles/Form.css';
import '../Styles/Buttons.css';

function Form() {
  const [code, setCode] = useState();
  return (
    <form className='form flex'>
      <div className='form_header flex'>
        <p>Contact</p>
        <p>Us</p>
      </div>
      <div className='form_inputs flex'>
        <div class="input_group">
          <input className='input_body' type="text" name='' id="dynamic-label-input" placeholder="First Name" />
          <label className='input_label' for="dynamic-label-input">First Name</label>
        </div>
        <div class="input_group">
          <input className='input_body' type="text" id="dynamic-label-input" placeholder="Last Name" />
          <label className='input_label' for="dynamic-label-input">Last Name</label>
        </div>
        <div class="input-select_group">
          <input className='input_body' type="tel" id="dynamic-label-input" placeholder={!code ? '+1' : code} />
          <label className='input_label' for="dynamic-label-input">{!code ? '+1' : code}</label>
          <select className='select_body' value={code} onChange={e => setCode(e.target.value)}>
            <option className='option_usa' selected value='+1'>US</option>
            <option className='option_ukr' value='+44'>UK</option>
            <option className='option_ukr' value='+38'>UA</option>
          </select>
        </div>
        <div class="input_group">
          <input className='input_body' type="email" id="dynamic-label-input" placeholder="Enter Email" />
          <label className='input_label' for="dynamic-label-input">Enter Email</label>
        </div>
        <div class="textarea_group">
          <textarea className='textarea_body' type="text" id="dynamic-label-input" placeholder="Tell Us What Happened" />
          <label className='textarea_label' for="dynamic-label-input">Tell Us What Happened</label>
        </div>
      </div>
      <div className='button_wrapper'>
        <button className='button black_button'>Send</button>
        <div>Send</div>
      </div>
    </form >
  )
}

export default Form;